'use strict';

class ChartContainer extends React.Component {
	constructor() {
		super();
		this.state = { data: [] }
	}
	componentDidMount() {
		$.ajax({
			url: "data.json",
			dataType: 'json',
			success: function(data) {
				this.setState({data: data});
			}.bind(this)
		});
	}
	render() {
		console.log('something');
		console.log(this.props.type);
		return <Chart data={this.state.data} />;
	}
}
class Chart extends React.Component {
	constructor(props) {
		super(props);
	}
	render() { 
		return <div className="chart-container"> {this.renderChart({chartType: 'pie', chartData: this.props.data})} </div>;
	}
	renderChart({ chartType, chartData }) {
		let modifiedData = {};
		let chartKeys = [];

		this.props.data.forEach(elem => {
			chartKeys.push(elem.label);
			modifiedData[elem.label] = elem.value;
		});

		c3.generate({
			bindto: '.chart-container',
			data: {
				json: [ modifiedData ],
				keys: {
					value: chartKeys
				},
				type: chartType
			}
		});
	}
}


React.render(
	<ChartContainer type="pie" />,
	document.querySelector('#container')
	);


